<template>
  <div id="rechange">
    <div class="index">
      <img src="../../assets/loog-1.png" alt="">
      <ul>
        <router-link :to="{path:'/index'}" tag="li" active-class="act">首页</router-link>
        <router-link :to="{path:'/mycar'}" tag="li" active-class="act">车队</router-link>
        <router-link :to="{path:'/8'}" tag="li" active-class="act">运单</router-link>
        <router-link :to="{path:'/7'}" tag="li" active-class="act">支付</router-link>
        <router-link :to="{path:'/invoices'}" tag="li" active-class="act">发票</router-link>
        <router-link :to="{path:'/5'}" tag="li" active-class="act">保理</router-link>
        <router-link :to="{path:'/account'}" tag="li" active-class="act">账户</router-link>
        <router-link :to="{path:'/stem'}" tag="li" active-class="act">设置</router-link>
        <router-link :to="{path:'/account'}" tag="li" active-class="act">消息</router-link>
      </ul>
      <div class="index-left">
        <span>登录</span>
        <span>注册</span>
      </div>
    </div>
    <router-view class="content"></router-view>
  </div>
</template>

<script>
export default {
  name: 'app',
  components:{
  },
}
</script>

<style lang="less" scoped>
#rechange{
  width: 100%;
  height: 1000px;
}
.index{
  width: 1200px;
  height: 80px;
  background: #fff;
  color: #323232;
  position: absolute;
  top: 0;
  left: 80px;
 img{
   float: left;
 }
 ul{
   width: 900px;
   float: left;
   height: 80px;
   li{
     margin-left:50px;
     height: 70px;
     float: left;
     text-align: center;
     line-height: 80px;
     font-size: 18px;
   }
 }
 .index-left{
   float: right;
   width: 100px;
   text-align: center;
   line-height: 80px;
   span{
     width: 50px;
     float: left;
     display:block;
     color: #9b9b9b;
   }
 }
}
.act{
  color: #2e53ac;
  border-bottom:  3px  solid #2e53ac;
}
.content{
  // border: 1px solid;
  margin: 0 auto;
}
</style>



